@import './reset.scss';

$maincolor : #ff0000;

@mixin my_alert_function(){
    background-color: #eee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: auto;
}

.red{
    color: $maincolor;
    font-size: 30px;
}

.my_alert {
    @include my_alert_function();
}

.my_alert_red {
    @extend .my_alert;
    background-color: #ffe591;
}


.my_alert p {
    margin-bottom: 0;
}

